//regular color #8B4389
export var changeMainTheme= () => {
    colorTheme.primaryColor = "#FFD000"
}

export var resetMainTheme = () => {
    colorTheme= {
        primaryColor: "#8B4389",
        primaryBackgroundColor: "white",
        primaryTextColor: "black",
        secondaryTextColor: "white"
    }
    
    
}

