import React from 'react';
import "../style.css"
import { PointTrackers } from "../reused-components/pointTrackers.js";
import { SideNav, Navbar } from "../reused-components/navigation.js";
import { CloseIcon, LessonIcon } from "../reused-components/icons.js";
import { LinearProgress, withStyles } from '@material-ui/core';
import { changeLessonHomepage, isGuest, getAvailableJourneyMapLessons } from '../backend';
import { CheckpointPopUp, ExpandIcon, getCheckPointsAndLessons } from "./JourneyPage.js";
import RetiMap from "./RETI_map.svg"

export class JourneyMapRETI extends React.Component {
  constructor(props) {
    super(props)
    this.state = { checkPoint: 0, availableLessonsLastAvailableCheckpoint: 0 }
    this.checkLessonAvailability()
  }
  openCheckpointPopUp = (checkpoint) => {
    this.setState({ currentCheckpoint: checkpoint, popUp: true })
  }
  checkLessonAvailability = async () => {
    var availableJourneyMapLessons = await getAvailableJourneyMapLessons()
    var checkPoint = (await getCheckPointsAndLessons(availableJourneyMapLessons))[0]
    var availableLessonsLastAvailableCheckpoint = (await getCheckPointsAndLessons(availableJourneyMapLessons))[1]
    //alert(checkPoint)
    this.setState({ checkPoint: checkPoint, availableLessonsLastAvailableCheckpoint: availableLessonsLastAvailableCheckpoint })
  }
  checkPointColor = (checkpoint) => {
    if (checkpoint <= this.state.checkPoint) {
      return "#93c361"
    }
    else {
      return "red"
    }
  }
  render() {
    var journeyPoints = new Array(24).fill("Test text")
    journeyPoints = journeyPoints.map((item, index) => `${index + 1}. ${item}`)
    return (
      <>
        {this.state.popUp ? <CheckpointPopUp
          checkpoint={this.state.currentCheckpoint}
          checkpointUnlocked={this.state.currentCheckpoint <= this.state.checkPoint}
          availableLessons={this.state.currentCheckpoint < this.state.checkPoint ? 10000 : this.state.availableLessonsLastAvailableCheckpoint}
          changePopUp={() => this.setState({ popUp: false })}
        /> : null}
        {//<Checklist />
        }
      <img src={RetiMap} width="80%"/>

      </>
    )
  }
}






